/*  Typography
	========================================================================== */
h1,
.WCEU2023-HeadingOne {
	font-family: $Philosopher !important;
	font-weight: $Bold !important;
	color: $WCEU2023-Red-100;
}

/* 3.1.2 Heading 2 */
h2,
.WCEU2023-HeadingTwo {
	font-family: $Philosopher !important;
	font-weight: $Bold !important;
	color: $WCEU2023-Red-100;
}

/* 3.1.3 Heading 3 */
h3,
.WCEU2023-HeadingThree {
	font-family: $Mulish;
	font-weight: $SemiBold;
	color: $WCEU2023-DarkBlue-100;
}

/* 3.1.4 Heading 4 */
h4,
.WCEU2023-HeadingFour {
	font-family: $Mulish;
	font-weight: $Medium;
	color: $WCEU2023-DarkBlue-100;
}

/* 3.1.5 Heading 5 */
h5,
.WCEU2023-HeadingFive {
	font-family: $Mulish;
	font-weight: $Medium;
	color: $WCEU2023-DarkBlue-100;
}

/* 3.1.6 Heading 6 */
h6,
.WCEU2023-HeadingSix {
	font-family: $Mulish;
	font-weight: $SemiBold;
	color: $WCEU2023-DarkBlue-100;
}

/* 3.2 Paragraph - Body Block Text */
.WCEU2023-BlockText,
p {
	font-family: $Mulish;
	font-weight: $SemiBold;
	color: $WCEU2023-DarkBlue-100;
}
